import React from 'react';
import URLSearchParams from '@ungap/url-search-params';
import { Route } from 'react-router-dom';
import { Accordion, Card, Toast, Pagination, Button } from 'react-bootstrap';
import graphQLFetch from './graphQL.js';
import IssueFilter from './IssueFilter.jsx';
import IssueTable from './IssueTable.jsx';
import IssueAdd from './IssueAdd.jsx';
import IssueDescription from './IssueDescription.jsx';
import store from './store.js';
import withToast from './withToast.jsx';
import { LinkContainer } from 'react-router-bootstrap';

const SECTION_SIZE = 5;
function PageLink({params, page, activePage, children}) {
  params.set('page',page);
  if(page==0) return React.cloneElement(children, {disabled:true});
  return(
    <LinkContainer 
      isActive={ ()=> page == activePage }
      to= {{ search: `?${params.toString()}` }} >
        {children}
      </LinkContainer>  
      
  ); 
}
class IssueList extends React.Component {
  constructor() {
    super();
    const issues = store.intialData ? store.intialData.issues : null;
    const selectedIssue = store.intialData ? store.intialData.issue : null;
    const pages = store.intialData ? store.intialData.pages : null;
    delete store.intialData;
    this.state = {
      issues,
      selectedIssue,
      pages
    };
    this.createIssue = this.createIssue.bind(this);
    this.closeIssue = this.closeIssue.bind(this);
    this.deleteIssue = this.deleteIssue.bind(this);
  }

  static async fetchData(match, search, showError){
    const param = new URLSearchParams(search);
    let page = parseInt(param.get('page'),10);
    if(Number.isNaN(page)) page=1;
    const vars = {hasSelection: false, selectedId: 0  };
    vars.page=page;
    const {params:{ id } } = match;
    const idInt = parseInt(id,10);
    if(!Number.isNaN(idInt)){
      vars.hasSelection = true;
      vars.selectedId = idInt; 
    }
    if (param.get('status')) {
      vars.status = param.get('status');
    }
    const effortMin = parseInt(param.get('effortMin'), 10);
    if (!Number.isNaN(effortMin)) {
      vars.effortMin = effortMin;
    }
    const effortMax = parseInt(param.get('effortMax'), 10);
    if (!Number.isNaN(effortMax)) {
      vars.effortMax = effortMax;
    }
    const query = `
          query issueList(
            $hasSelection:Boolean!
            $selectedId: Int!
            $status:StatusType
            $effortMin: Int
            $effortMax: Int
            $page: Int
            ){
              issueList(
                status:$status
                effortMin: $effortMin
                effortMax: $effortMax
                page: $page
                ){
                  issues {
                    id
                    status
                    owner
                    title
                    effort
                    created
                    due
                   }
                  pages
              }
              issue(id: $selectedId) @include (if: $hasSelection) {
                id description
              }
          }`;
    const data = await graphQLFetch(query, vars, showError);
    return data;
  }

  async componentDidMount() {
    const { issues } = this.state;
    if ( issues==null ){
      this.loadData();
    }
  }

  componentDidUpdate(prevProps) {
    const { location: { search: prevSearch }, 
    match:{params: {id: prevId}}} = prevProps;
    const { location: { search }, match:{ params: { id } } } = this.props;
    if (prevSearch !== search || prevId!==id  ) {
      this.loadData();
    }
  }

  async loadData() {
    const {location :{ search }, match, showError } = this.props;
    const data = await IssueList.fetchData(match, search, showError);
    if (data) {
      this.setState({ issues: data.issueList.issues, selectedIssue: data.issue, pages: data.issueList.pages   });
    }
  }

  async closeIssue(index) {
    const query = `mutation issueClose($id: Int!){
      issueUpdate(id: $id, changes: {status: Closed}) {
        id title status owner 
        effort created due description
      }
    }`;
    const { issues } = this.state;
    const { showError, showSucess } = this.props;
    const data = await graphQLFetch(query, { id: issues[index].id }, showError);
    if (data) {
      this.setState((prevState) => {
        const newList = [...prevState.issues];
        newList[index] = data.issueUpdate;
        return { issues: newList };
      });
      showSucess('Issue closed');
    } else {
      this.loadData();
    }
  }

  async deleteIssue(index) {
    const query = `mutation issueDelete($id: Int!){
      issueDelete(id: $id)
    }`;
    const { issues } = this.state;
    const { location: { pathname, search }, history , showError, showSucess } = this.props;
    const { id } = issues[index];
    const data = await graphQLFetch(query, { id }, showError);
    if (data && data.issueDelete) {
      this.setState((prevState) => {
        const newList = [...prevState.issues];
        if (pathname === `/issues/${id}`) {
          history.push({ pathname: '/issues', search });
        }
        newList.splice(index, 1);
        return { issues: newList };
      });
      const undo_msg = (
        <span>
          {`Deleted issue ${id} sucessfully.`}
          <Button variant="link" onClick = {()=> this.restoreIssue(id)}> UNDO </Button>
        </span>
      );
      showSucess(undo_msg);
    } else {
      this.loadData();
    }
  }
  async restoreIssue(id) {
    const query = `mutation issueRestore($id: Int!){
      issueRestore(id: $id)
    }`;
    const { showSucess, showError} = this.props;
    const data = await graphQLFetch(query,{ id }, showError);
    if(data) {
      showSucess(`Issue ${id} restored successfully.`);
      this.loadData();
    }
  }
  async createIssue(issue) {
    const query = `mutation issueAdd($issue:IssueInputs!){
                  issueAdd(issue:$issue){
                      id
                  }
              }`;
    const { showError, showSucess} = this.props;
    const data = await graphQLFetch(query, { issue }, this.showError);
    if (data) {
      this.loadData();
      showSucess('Issue Added sucessfully');
    }
  }

  render() {
    const { issues } = this.state;
    const { selectedIssue, pages } = this.state;
    const { location: { search } } = this.props;
    const params = new URLSearchParams(search);
    let page = parseInt(params.get('page'),10 );
    if(Number.isNaN(page)) page=1;
    const startPage = Math.floor((page-1)/SECTION_SIZE) * SECTION_SIZE + 1;
    const endPage = startPage + SECTION_SIZE - 1;
    const prevSection = startPage === 1 ? 0 : startPage -SECTION_SIZE;
    const nextSection = endPage >= pages ? 0 :startPage + SECTION_SIZE;
    const items = [];
    for(let i=startPage; i<=Math.min(endPage,pages); i++){
      params.set('page',i);
      items.push((
        <PageLink key={i} params={params} activePage={page} page={i}>
          <Pagination.Item>{i}</Pagination.Item>
        </PageLink>
      ));
    }
    if(issues == null) return null;
    const { match } = this.props;
    return (
      <>
        <Accordion defaultActiveKey="0">
          <Card>
            <Accordion.Toggle as={Card.Header} eventKey="0">
              <Card.Title>Filter</Card.Title>
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="0">
              <Card.Body>

                <IssueFilter urlBase="/issues"/>
              </Card.Body>
            </Accordion.Collapse>
          </Card>
        </Accordion>

        <IssueTable issues={issues} closeIssue={this.closeIssue} deleteIssue={this.deleteIssue} />
        <IssueAdd createIssue={this.createIssue} />
        <IssueDescription issue={selectedIssue} />
        <Pagination>
          <PageLink params={params} page={prevSection}>
            <Pagination.Item>{'<'}</Pagination.Item>
          </PageLink>
          {items} 
          <PageLink params={params} page={nextSection}>
            <Pagination.Item>{'>'}</Pagination.Item> 
          </PageLink>
        </Pagination>
      </>
    );
  }
}
const IssueListWithToast = withToast(IssueList);
IssueListWithToast.fetchData = IssueList.fetchData;
export default IssueListWithToast;
